<template>
  <div class="summary">
    <div class="summarywrapper">
      <Row :gutter="30" class-name="top">
        <Col span="12" class-name="topleft">
          <Row class-name="toplefttop">
            <Col span="12">
              <div class="topleftt1">
                <div class="wrapper">
                  <span>{{recovery}}</span>
                  <p>回收总数据</p>
                </div>
              </div>
            </Col>
            <Col span="12">
              <div class="topleftt2">
                <div class="wrapper">
                  <span>{{count}}</span>
                  <p>问卷总浏览量</p>
                </div>
              </div>
            </Col>
          </Row>
          <Row>
            <Col span="24">
              <div class="topleftbottom">
                <div class="inner">
                  <Row>
                    <Col span="8">创建时间</Col>
                    <Col span="10" offset="6" class-name="time">{{creatdate}} <span>{{creattime}}</span></Col>
                  </Row>
                  <Row>
                    <Col span="8">修改时间</Col>
                    <Col span="10" offset="6" class-name="time">{{updateddate}} <span>{{updatedtime}}</span></Col>
                  </Row>
                  <Row>
                    <Col span="8">总问题数</Col>
                    <Col span="10" offset="6" class-name="time">{{qnum}}题</Col>
                  </Row>
                  <Row>
                    <Col span="8">问卷状态</Col>
                    <Col span="10" offset="6" class-name="time timelast">
                       <span class="switchspan"><i-switch @on-change="changecollect()" v-model="iscollect" size="small"></i-switch></span><span v-if="iscollect">收集中</span><span v-if="!iscollect">未收集</span>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </Col>
        <Col span="12">
          <div class="topright">
            <div id="main"></div>
          </div>
        </Col>
      </Row>
      <Row class-name="bottom">
        <Col span="24">
          <div class="bottomwrapper">
            <p class="bottomtitle">
              最近提交数据
<!--               <span @click="clear()">清空数据</span>
              <span @click="del">选中删除</span>
 -->           </p>
           <p class="exporttext" @click="exportData()">
             <img src="./export.png">
             <span>导出</span>
           </p>
           <Table height="400" border :columns="columns8" :data="data7" ref="table" @on-selection-change="del"></Table>
          </div>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import echarts from 'echarts'
export default {
  data () {
    return {
      count: '',
      creatdate: '',
      creattime: '',
      updateddate: '',
      updatedtime: '',
      recovery: '',
      qnum: '',
      iscollect: false,
      datacollection: null,
      columns8: [],
      data7: [
      // {
      //   25: "1",
      //   26: "12, 1",
      //   27: "112",
      //   28: "18916119446",
      //   29: "asfdafds",
      //   30: "1",
      //   31: "B, A, C"
      // }
      ],
      charts: '',
      opinion: ['2017-07-09', '2017-07-09', '2017-07-09', '2017-07-09'],
      opinionData: [100, 180, 110, 555]
    }
  },
  mounted () {
    this.$nextTick(function () {
      this.drawPie('main')
    })
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$http.get('loginStatus').then(response => {
        if (response.body.status === 0) window.location.href="http://survey.twtstudio.com/api/login"
        else {
      vm.$http.get('minequestion/overview/' + vm.$route.params.id).then((response) => {
        console.log(response.body)
        vm.$store.commit('savetopic', response.body.questionnaire_data.name)
        var timearr = response.body.questionnaire_data.created_at.split(' ')
        vm.creatdate = timearr[0]
        vm.creattime = timearr[1].slice(0, 5)
        var uptimearr = response.body.questionnaire_data.updated_at.split(' ')
        vm.updateddate = uptimearr[0]
        vm.updatedtime = uptimearr[1].slice(0, 5)
        vm.count = response.body.questionnaire_data.num
        vm.recovery = response.body.questionnaire_data.recovery
        if (response.body.questionnaire_data.status === 1)
          vm.iscollect = true
        else
          vm.iscollect = false
        vm.columns8 = [
        // {
        //   type: 'selection',
        //   width: 60,
        //   align: 'center'
        // },
        {
          type: 'index',
          width: 60,
          align: 'center'
        }
        ]
        vm.qnum = response.body.questionnaire_data.qcount
        for (var s = 0; s < response.body.questions.length; s++) {
          vm.columns8.push({
            title: response.body.questions[s].topic,
            key: response.body.questions[s].qid,
            width: 150
          })
        }
        var data1=[], data2=[]
        for (var i in response.body.everyday_ans) {
          data1[i] = response.body.everyday_ans[i].number
          data2[i] = response.body.everyday_ans[i].time
        }
        // console.log(data1, data2)
        vm.opinion = data2
        vm.opinionData = data1
        vm.drawPie('main')

        // vm.data7 = response.body.answer
        var ans = response.body.answers
        console.log(ans)
        for (var i = 0; i < ans.length; i++) {
          data1[i] = {}
          for (var j = 0; j < ans[i].length; j++ ) {
            if (ans[i][j].qtype == 10) {
              data1[i][ans[i][j].qid] = ans[i][j].answer[0].option + ans[i][j].answer[1].option+''
            }
            else if (ans[i][j].qtype == 1 || ans[i][j].qtype == 6) {
              data1[i][ans[i][j].qid] = ans[i][j].answer.join('，')+''
            }
            else {
              data1[i][ans[i][j].qid] = ans[i][j].answer+''
            }
          }
        }
        vm.data7 = data1
      })
        }
      })
    })
  },
  methods: {
    // clear () {
    //   this.$http.get('minequestion/overview/' + id, {allkilled: id}).then((response) => {
    //     this.$Message.success('已保存设置')
    //   })
    // }
    del (current, old) {
      console.log(current)
    },
    changecollect () {
      var id = this.$route.params.id
      if (this.iscollect)
        var uprecoveryat = 1
      else 
        var uprecoveryat = 0
      this.$http.post('minequestion/install/' + id, { recoveryat: uprecoveryat }).then((response) => {
        this.$Message.success('已保存设置')
      })
    },
    exportData () {
      this.$refs.table.exportCsv({
        filename: this.$store.getters.gettopic+'问卷数据',
        columns: this.columns8.filter((col, index) => index >1)
      })
    },
    drawPie (id) {
      this.charts = echarts.init(document.getElementById(id))
      this.charts.setOption({
        title: {
          text: '问卷回收量',
          left: '30',
          top: '15',
          textStyle: {
            color: '#707070',
            fontWeight: '100',
            fontSize: '15'
          }
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.opinion
        },
        yAxis: {
          splitLine: {
            show: true
          },
          offset: 100,
          interval: 200
        },
        series: [
          {
            name: '访问来源',
            type: 'line',
            data: this.opinionData,
            // data: [1,1,1,1,1],
            lineStyle: {
              normal: {
                color: '#00a1e9',
                width: 1
              }
            },
            itemStyle: {
              normal: {
                color: '#00a1e9',
                borderWidth: 4
              }
            },
            symbol: 'circle',
            symbolSize: 10,
            label: {
              normal: {
                show: true,
                color: '#707070'
              }
            }
          }
        ]
      })
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.summary
  // height 100%
  background-color rgba(233,234,238,0.52)
  position relative
  top 130px
  // overflow-y auto
  overflow-x hidden
.summarywrapper
  max-width 1160px
  height 100%
  margin 0 auto
  padding-top 35px
  padding-left 20px
  padding-right 20px
  // padding-bottom 100px
.topleft 
  margin-left -15px
.topleftt1,.topleftt2
  background-color white
  border 1px solid #dadada
  height 160px
  text-align center
  padding-top 50px
  box-sizing boder-box
.topleftt1 span,.topleftt2 span
  font-size 25px
.topleftbottom
  margin-top 40px
  background-color white
  border 1px solid #dadada
  height 160px
  padding 20px 0
  box-sizing boder-box
.topright
  background-color white
  border 1px solid #dadada
  height 360px
  overflow-x auto
  margin-right -15px
.top
  margin-bottom 35px
.inner
  height 100%
  width 82%
  margin 0 auto
  font-size 14px
  line-height 30px
  overflow auto
.inner span
  margin-left 20px
.time 
  text-align right
.timelast
  color #00a1e9
.bottom
  margin 0 auto
  background-color red
  background-color white
  border 1px solid #dadada
  height 550px
  margin-bottom 150px
.bottomwrapper
  margin 40px auto
  width 90%
  height 100%
.bottomtitle
  font-size 17px
.bottomtitle span
  font-size 14px
  position relative
  bottom 1px
.bottomtitle span:firsΩt-child
  margin-left 130px
.bottomtitle span
  margin-left 30px
  color #00a1e9
  cursor pointer
.exporttext
  text-align right
  font-size 14px
  cursor pointer
  margin-top 10px
  margin-bottom 20px
.exporttext img
  width 22px
  vertical-align middle
  position relative
  bottom 1px
  cursor pointer
.small
  padding 25px
#main
  height 370px
  width 580px
  position relative
  right 10px
.switchspan
  position relative
  bottom 1px
</style>


